



















































import { Component, Vue } from 'vue-property-decorator';
const io = require('socket.io-client');
const socket = io('http://localhost:3000');

@Component
export default class App extends Vue {
  veterinarians: Array<any> = [];
  customers: Array<any> = [];
  pets: Array<any> = [];

  mounted() {
    socket.on('veterinarians', (data: any) => {
      this.veterinarians = data;
    });
    socket.on('customers', (data: any) => {
      this.customers = data;
    });
    socket.on('pets', (data: any) => {
      this.pets = data;
    });
    socket.emit('veterinarians');
    socket.emit('customers');
    socket.emit('pets');
  }
}
